@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);

$mobile-length: 660px;
$button-size: 35px;
$scrollbar-width: 10px;

@mixin step1( $width, $color ) {
	box-shadow:
	$width 0 $color,
	-$width 0 $color,
	0 (-$width) $color,
	0 $width $color;
	margin: 2*$width auto;
}



@media (min-width:320px) {
	#FroggyDressUp{
		width:100%;
		height:1500px;
		min-width: 1000px+$scrollbar-width;
	}
	.frogTop{
		width:100%;
		height: 15%;
		background: url('../img/newbigbgtop.png') no-repeat;
		background-size: 100% 100%;
		image-rendering: pixelated;
	}
	.frogMiddle{
		width:100%;
		height: 90%;
		background: url('../img/newbigbgmiddle.png') no-repeat;
		background-size: 100% 100%;
		image-rendering: pixelated;
	}
	.frogBottom{
		width: 100%;
		height: 15%; 
		background: url('../img/newbigbgbottom.png') no-repeat;
		background-size: 100% 100%;
		image-rendering: pixelated;
	}
	#frogContainer{
		position:relative;
		/* figure out where to put everything */
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	
		width: $mobile-length;
		height: 2*$mobile-length;
		border: 5px solid black; 
		margin: 0px;
		flex-direction: column;
		display:flex;
		overflow: visible;
	}
	#COLORButton{
		left: 9px;

		
	}
	#BGButton{
		left:59px;
	}
	
	#FACEButton{ 
		left: 135px;
		/*make it look nice and put it in the right spot*/
	}
	
	#BODYButton{
		left: 265px;
		/*make it look nice and put it in the right spot*/
	}
	
	#HATButton{ 
		left: 395px; 
		/*make it look nice and put it in the right spot*/
	}
	
	#ACCButton{ 
		left: 498px;
		/*make it look nice and put it in the right spot*/
	}
	
	#CButton{ 
		left:600px;
		/*make it look nice and put it in the right spot*/
	}

	#colorIcon{
		width: 25px;
		height: 25px;
		padding-bottom: 3px;
	} 
	#frog{ 
		/* so this is where the frog should go but 
		 *also we have to put the parts separately 
		 */
		 position:relative;
		 float: left;
		 left: 0px;
		 width: $mobile-length;
		 height: $mobile-length;
		 //border: 0px solid black;
		 //border-right: 5px solid black;
		 //transform: translate(-px, -5px);
		 margin-right: 0px;
	}
	
	#optionContainer{
		position: relative;
		float: right;
		left:0px;
		width: $mobile-length + $scrollbar-width;
		height: $mobile-length;
		margin: 0px;
	
		overflow-y: scroll;
		scrollbar-width: thin;
		scrollbar-color: #10aa60 transparent;
		border-top: 5px solid black;
		/* select the position of the grid */ 
	}
	.border{
		position: absolute; 
		z-index: 10;
		right:0px;
		height: 0; 
		width: $mobile-length;
		border: 2.5px solid black;
	}

	.gridimg{ 
		position: relative;
		width: $mobile-length/3-4;
		height: $mobile-length/3-4;
		z-index: 1;
	}

	#overlay{
		position: absolute;
		width: $mobile-length/3-4;
		height: $mobile-length/3-4;
		opacity: 0;
		z-index: 3;
		left: 50%;
		top: 50%; 
		transform: translate(-50%, -50%);
	}

	#overlay:hover, :focus{
		opacity: 1;
	}

	.gridelem{
		position: relative;
		border: 2px solid black;
		margin: 0px;
	
		width: $mobile-length/3-4;
		height: $mobile-length/3-4;
	}

	.placeholderFrogForBg{
		position: absolute; 
		width: $mobile-length/3-4;
		height: $mobile-length/3-4;
		z-index: 2;
	}
	
	.placeholderFrog{
		position: absolute; 
		width: $mobile-length/3-4;
		height: $mobile-length/3-4;
		z-index: 0;
	}

	.options{ 
		/*make it look nice and put it in the right spot*/
		position: absolute;
		bottom: -6px;
		//top: 100px;
		background: black;
		color: white;
		//writing-mode: vertical-rl;
		//text-orientation: upright;
		@include step1( 3px, black );
		border: 0px;
		//box-shadow: inset (-2px) (2px) 0px 0px black;
		box-sizing: border-box;
		font: 1.7em "Press start 2P";
		z-index: 5;
		top: $mobile-length - 3*$button-size;
	}

	.options:hover, :focus{
		background: white;
		color: black;
	}

	@media screen and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
		.topdown{
			margin-right: 0px;
			margin-left: 0px;
			margin-top: 0px; 
			margin-bottom: 0px;
		}
	}

	.frogBg{
		position: absolute;
		z-index: 0;
		width: $mobile-length;
		height: $mobile-length;
	}
	
	.frogBase{
		position: absolute;
		z-index: 1;
		width: $mobile-length;
		height: $mobile-length;
	}
	
	.frogBody{
		position: absolute;
		z-index: 3;
		width: $mobile-length;
		height: $mobile-length;
	}
	
	.frogFace{
		position: absolute;
		z-index: 2;
		width: $mobile-length;
		height: $mobile-length;
	}
	
	.frogAcc{
		position: absolute;
		z-index: 4;
		width: $mobile-length;
		height: $mobile-length;
	}
	
	.frogHat{
		position: absolute;
		z-index: 5;
		width: $mobile-length;
		height: $mobile-length;
	}
	
	
	
	.coverup{
		position:fixed;
		z-index: 11;
		background-color: beige;
		width: 100%;
		height: 100%;
		top: 0px;
		left: 0px;
	}
	
	#finalFrogge{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 510px; 
		height: 510px;
	}

	.bigButton{
		/*
		left: 7%;
		position: absolute;
		transform: translate(-50%, -50%);
		background: greenyellow;
		color: black;
		font: 1.4em "Press start 2P";
		@include step1(5px, rgb(68, 204, 68));
		border: 0px;
		*/
		border: 0px;
		position: relative;
		margin: 0px;
		top: 0;
		left: 50%;
		width: 150px;
		height: 60px;
		background-repeat: no-repeat;
		background-size: 100% 100%;
	}
	
	.bigText{
		margin-top: 5px;
		margin-bottom: 5px;
	}
	
	#SAVEButton{
		background-image: url("../img/savebutton.png");

		left: 50%;
		transform: translate(-200%, -25%);
	}

	#SAVEButton:hover, #SAVEButton:focus{
		background-image: url("../img/savehover.png");
	}
	
	#RESETButton{
		transform: translate(-10%, -25%);
		background-image: url("../img/resetbutton.png");
	}

	#RESETButton:hover, #RESETButton:focus{
		background-image: url("../img/resethover.png");
	}
	
	#BACKButton{
		top: 50%;
		height: 100px;
		width: 172px;
		transform: translate(-50%, -100%);
		z-index: 100;
		background-image: url("../img/backbutton.png");
	}

	#BACKButton:hover, #BACKButton:focus{
		background-image: url("../img/backhover.png");
	}
	
	#finalFrogge{
		top: 25%;
		image-rendering: pixelated;
		z-index: 50;
		width: $mobile-length;
		height: $mobile-length;
	}
	
}


